
.inline-blocks > .block{
  display: inline-block;
  vertical-align: middle;
}

.image{
  height: 200px;
  width: 170px;
  border-radius: 180px;
  border: 3px solid #fff;
  margin-top: 70px;
  margin-right: 80px;
  margin-left: 30px;
}

.btn-circle {
    width: 30px;
    height: 30px;
    padding: 6px 0px;
    border-radius: 15px;
    text-align: center;
    font-size: 12px;
    line-height: 1.42857;
}

.image-mobile{
  height: 100px;
  width: 80px;
  border-radius: 90px;
  border: 2px solid #fff;
  /*margin-top: 20px;*/
  margin-top: 170px;
  margin-bottom: 100px;
  margin-right: 20px;
  margin-left: 6px;
}

.twenty-one{
  font-size: 300px;
  font-family: 'Josefin Sans';
  font-weight: 100;
  line-height: 180px;
  color: #fff;
  margin-top: 170px;
  margin-bottom: 100px;
}

.twenty-one-mobile{
  font-size: 150px;
  font-family: 'Josefin Sans';
  font-weight: 100;
  line-height: 180px;
  color: #fff;
  margin-top: 170px;
  margin-bottom: 80px;
}


.bday{
  margin-top: 60px;
  font-size: 40px;
  color: #fff;
  font-family: 'Josefin Sans';
  font-weight: 300;
  text-align: center;
}

.bday-mobile{
  margin-top: 10px;
  font-size: 40px;
  color: #fff;
  font-family: 'Josefin Sans';
  font-weight: 300;
  text-align: center;
}

.cover{
  background: rgba(20,20,20,0.82);
  position: absolute;
  width: 100%;
  height: 100%;
  top:0;
  left: 0;
}

#can{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.super-centered-container{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.overlay-static{
  position: relative;
  /*z-index: 1000;*/
  margin-bottom: 250px;
}

.overlay-static-mobile{
  position: relative;
  /*z-index: 1000;*/
  margin-bottom: 100px;
}


.demabg {
  position: absolute;
  background-image: url('demabg.png');
  /*background-repeat:no-repeat;*/
  background-size:contain;
  background-position: center center;
  overflow:hidden;
  width: 100%;
